<template>
  <div id="app">
    <div class="header">
      <div class="logo"><img src="" /></div>
      <ul class="nav">
        <li>
          <router-link to='/' exact tag='div' active-class='active1'>
            首页
          </router-link>
        </li>
        <li>
          <router-link to='/about' event='mouseover'>
            关于我们
          </router-link>
        </li>
        <li>
          <router-link to='/document'>
            文档
          </router-link>
        </li>
      </ul>
    </div>

    <router-view class="center"></router-view>

  </div>
</template>

<script>
export default {
  name: 'app'
}
</script>

<style>
*{padding:0;margin:0;}
html,body,div,span,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,address,big,cite,code,del,em,font,img,ins,small,strong,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend{margin:0;padding:0;}
body{font:12px/150% Arial,Verdana,"宋体","微软雅黑";background:#ffffff;}
ul,ol,li{list-style-type:none;}
a,img,div,form,ul,ol,li,span,p,dl,dt,dd{border:0 none;}
a{text-decoration:none;color:#333;}
table,td,input,textarea{font-size:12px;}
h1,h2,h3,h4,h5,h6{font-size:12px;font-weight:normal;}
html{-webkit-box-sizing:border-box;-moz-box-sizing:border-box;box-sizing:border-box;}
html { -webkit-box-sizing:border-box; -moz-box-sizing:border-box; box-sizing:border-box; padding:90px 0 0; overflow:hidden;}
html,body { height:100%;}

#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
.header{
    position:relative;
    height:90px;
    background:#eff2f7;
    z-index:2;
    border-bottom:1px solid #d6dbdf;
    margin: -90px 0 0;
}
.logo {
    width: 454px;
    height: 90px;
    border-right: 1px solid #d6dbdf;
    float: left;
}
.nav{
  position:relative;
  height:90px;
  background:#eff2f7;
  z-index:2;
  border-bottom:1px solid #d6dbdf;
  float: left;
}
.nav li{
  width: 165px;
  height: 90px;
  border-right:1px solid #d6dbdf;
  cursor: pointer;
  float: left;
  line-height:90px;
  font-size: 20px;
  font-family: "微软雅黑";
}
.nav li a{
  width: 100%;
  height: 100%;
  display: block;
}
.activeAAA{
  color: #0fb1d6;
}
.active1{
  background:yellow;
  color:#000;
}
.center{
  font-weight:700;
}
.aa,.left_nav{
  font-size:50px;
}
.aa1{
  color:pink;
}
</style>



