import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import about from '@/components/about'
import document from '@/components/document'

Vue.use(Router)

export default new Router({
  mode: 'history',
  linkActiveClass: 'activeAAA',
  routes: [
    {
      path: '/',
      component: HelloWorld
    },
    {
      path: '/HelloWorld',
      component: HelloWorld,
      alias: 'index'
    },
    {
      path: '/about',
      name: 'About',
      component: about
    },
    {
      path: '/document',
      component: document
    },
    {
      path: '*',
      redirect: {name: 'About'}
    }
  ]
})



